























export default {
	data() {
		return {
			username: "",
			password: "",
			banner: [],
			current: 0,
		}
	},
	onLoad() {
		this.http({
			url: this.api.banner,
			method: "POST",
			data: {},
			success: res => {
				this.banner = res
			}
		})
	},
	methods: {
		change(e) {
			this.current = e.detail.current;
		},
		bind() {
			this.http({
				url: this.api.bind,
				method: "POST",
				data: {
					username: this.username,
					password: this.password,
					uid: this.common.get("uid")
				},
				success: res => {
					this.common.set('uid', res.uid)
					this.common.set('state', res.binding_state)
					uni.reLaunch({
						url: "/pages/index/index"
					})
				}
			})
		}
	}
}
